import React, { Component } from 'react';
import '@fortawesome/fontawesome-free/css/all.min.css';
import 'bootstrap-css-only/css/bootstrap.min.css';
import 'mdbreact/dist/css/mdb.css';
import './App.css';
import About from './components/About';
import Contacts from './components/Contacts';
import Forbidden from './components/Forbidden';
import Home from './components/Home';
import Pricing from './components/Pricing';
import scrollToComponent from 'react-scroll-to-component';
import {Navbar, Nav} from 'react-bootstrap'
import ReactGA from 'react-ga';
function initializeReactGA() {
  ReactGA.initialize('UA-56525651-39');
  ReactGA.pageview('/');
}

const data = {
  "@context": "http://www.schema.org",
  "@type": "Shipping Company",
  "name": "The Top Shelf LLC",
  "url": "https://thetopshelfllc.com",
  "logo": "https://thetopshelfllc.com/images/logo.png",
  "image": "https://thetopshelfllc.com/images/package2.jpg",
  "description": "Parcel Delivery From any location to the door step of your Loved Ones. ",
  "address": {
    "@type": "PostalAddress",
    "streetAddress": "Old Georgetown Rd",
    "addressLocality": "Bethesda",
    "addressRegion": "Maryland",
    "postalCode": "20814",
    "addressCountry": "United States"
  },
  "openingHours": "Mo, Tu, We 08:00-05:00 Th, Fr 08:00-06:00",
  "contactPoint": {
    "@type": "ContactPoint",
    "telephone": "+1(240)475-3267",
    "contactType": "Phone"
  }
}
const JsonLd = ({data}) =>
<script
  type='application/ld+json'
  dangerouslySetInnerHTML = {{__html: JSON.stringify(data)}}
/>

class App extends Component {

  render() {
  return (
    <div className="App">
        <JsonLd data={data} />
        <Navbar collapseOnSelect expand="lg" fixed="top">
          <a href="#home" className="navbar-brand" onClick={() => scrollToComponent(this.Home, { offset: 0, align: 'top', duration: 1500})}><img alt="logo" className="navbar-brand-image" src={process.env.PUBLIC_URL+"/images/logo.png"}/>TOP SHELF LLC</a> 
          <Navbar.Toggle aria-controls="responsive-navbar-nav" className="ml-auto"/>
          <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="ml-auto">
              <Nav.Link className="nav-link js-scroll-trigger" href="#home" onClick={() => scrollToComponent(this.Home, { offset: 0, align: 'top', duration: 1500})}>Home</Nav.Link>
              <Nav.Link className="nav-link js-scroll-trigger" href="#about-us-block" onClick={() => scrollToComponent(this.About, { offset: -70, align: 'top', duration: 1500})}>About us</Nav.Link>
              <Nav.Link className="nav-link js-scroll-trigger" href="#pricing" onClick={() => scrollToComponent(this.Pricing, { offset: -50, align: 'top', duration: 1500})}>Pricing</Nav.Link>
              <Nav.Link className="nav-link js-scroll-trigger" href="#contacts" onClick={() => scrollToComponent(this.Contacts, { offset: 0, align: 'top', duration: 1500})}>Contacts</Nav.Link>
            </Nav>
          </Navbar.Collapse>
        </Navbar>
        <section className='home' ref={(section) => { this.Home = section; }}><Home ref={(section) => { this.Home = section; }}/></section>
        <section className='about' ref={(section) => { this.About = section; }}><About/></section>
        <section className='pricing' ref={(section) => { this.Pricing = section; }}><Pricing/></section>
        <section className='forbidden' ref={(section) => { this.Forbidden = section; }}><Forbidden/></section>
        <section className='contacts' ref={(section) => { this.Contacts = section; }}><Contacts/></section>
    </div>
  );
}
}
export default App;

